Index.getInitialProps = async (ctx:any) => {
  const { data, error, loading } = await ctx.apolloClient.query({
    query: gql`
      {
        country(code: "SK") {
          name
        }
      }
    `,
    fetch
  });
  if (error) {
    return <div>Erreur</div>;
  }
  return { data, error, loading };
};


export default Index;
